
import { defineComponent, ref, watch } from "vue";
import { Form, Field, ErrorMessage, useForm, useField } from "vee-validate";
import ApiService from "@/core/services/ApiService";
import Swal from "sweetalert2/dist/sweetalert2.min.js";
import { Actions } from "@/store/enums/StoreEnums";
import { useStore } from "vuex";
import * as yup from "yup";

export default defineComponent({
  name: "Register",
  components: {
    Form,
    Field,
    ErrorMessage,
  },
  emits: ["register:active"],
  setup() {
    const store = useStore();
    const loading = ref(false);
    const name = ref("");
    const cif = ref("");
    const nif = ref("");
    const city = ref("");
    const postal_code = ref("");
    const country = ref("");
    const is_company = ref(true);
    const contact_name = ref("");
    const lastname = ref("");
    const address = ref("");
    const phone = ref("");
    const pass = ref("");
    const pass_verify = ref("");
    const email_verify = ref("");
    const submitButton = ref<HTMLElement | null>(null);

    const isNameRequired = (value) => {
      if (!value) {
        return "El nombre es requerido";
      }
      return true;
    };

    const isCifRequired = (value) => {
      if (!value) {
        return "El cif es requerido";
      }
      return true;
    };

    const isNifRequired = (value) => {
      if (!value) {
        return "El nif es requerido";
      }
      return true;
    };

    const isCityRequired = (value) => {
      if (!value) {
        return "La ciudad es requerida";
      }
      return true;
    };

    const isPostalCodeRequired = (value) => {
      if (!value) {
        return "El cdigo postal es requerido";
      }
      return true;
    };

    const isCountryRequired = (value) => {
      if (!value) {
        return "El pas es requerido";
      }
      return true;
    };

    const isContactNameRequired = (value) => {
      if (!value) {
        return "La persona de conatcto es requerida";
      }
      return true;
    };

    const isLastNameRequired = (value) => {
      if (!value) {
        return "El apellido es requerido";
      }
      return true;
    };

    const emailValidation = (value) => {
      if (!value) {
        return "El correo es requerido";
      }
      return true;
    };

    const emailVerifyValidation = (value) => {
      if (!value) {
        return "El email es requerido";
      } else if (value !== email.value) {
        return "Los Emails no coinciden";
      }
      return true;
    };

    const phoneValidation = (value) => {
      if (!value) {
        return "El telfono es requerido";
      }
      return true;
    };

    const passVerifyValidation = (value) => {
      if (!value) {
        return "La contrasea es requerida";
      } else if (value !== pass.value) {
        return "Las contraseas no coinciden";
      }
      return true;
    };

    const passValidation = (value) => {
      if (!value) {
        return "La contrasea es requerida";
      }
      return true;
    };

    watch(
      () => is_company.value,
      (first) => {
        name.value = "";
        cif.value = "";
        nif.value = "";
        city.value = "";
        postal_code.value = "";
        country.value = "";
        contact_name.value = "";
        lastname.value = "";
        address.value = "";
        phone.value = "";
        pass.value = "";
        pass_verify.value = "";
        email_verify.value = "";
      }
    );

    // Define a validation schema
    const schema = yup.object({
      email: yup.string().required().email(),
    });

    // Create a form context with the validation schema
    useForm({
      validationSchema: schema,
    });

    // No need to define rules for fields
    const { value: email, errorMessage: emailError } = useField("email");

    const register = (value) => {
      if (submitButton.value) {
        submitButton.value.setAttribute("data-kt-indicator", "on");
      }
      loading.value = true;
      ApiService.post("/api/customer", {
        params: {
          name: name.value,
          cif: cif.value,
          nif: nif.value,
          city: city.value,
          zip: postal_code.value,
          country: country.value,
          is_company: is_company.value,
          contact_name: contact_name.value,
          lastname: lastname.value,
          address: address.value,
          email: email.value,
          phone: phone.value,
          password: pass.value,
        },
      })
        .then(({ data }) => {
          loading.value = false;
          submitButton.value?.removeAttribute("data-kt-indicator");
          store.commit("setActive", "sheludes");
          store.commit("setRegister", false);
          store
            .dispatch(Actions.LOGIN, {
              email: email.value,
              password: pass.value,
            })
            .then(() => {
              store.dispatch(Actions.MY_PROFILE);
              loading.value = false;
              submitButton.value?.removeAttribute("data-kt-indicator");
              store.commit("setActive", "sheludes");
              store.commit("setLogin", false);
            });
        })
        .catch(() => {
          submitButton.value?.removeAttribute("data-kt-indicator");
        });
    };

    return {
      loading,
      emailError,
      name,
      cif,
      nif,
      city,
      postal_code,
      country,
      contact_name,
      is_company,
      lastname,
      address,
      phone,
      email,
      pass,
      pass_verify,
      submitButton,
      email_verify,
      register,
      isNameRequired,
      isCifRequired,
      isNifRequired,
      isCityRequired,
      isPostalCodeRequired,
      isCountryRequired,
      isContactNameRequired,
      isLastNameRequired,
      emailValidation,
      phoneValidation,
      passValidation,
      passVerifyValidation,
      emailVerifyValidation,
    };
  },
});
